import React, { useEffect } from 'react';
import Link from 'next/link';
import PropTypes from 'prop-types';
import { Col, Input, Menu, Row } from 'antd';
import { useSelector, useDispatch } from 'react-redux';
import LoginForm from './LoginForm';
import UserProfile from './UserProfile';
import { LOAD_USER_REQUEST } from '../reducers/user';


{/*js파일에 중복사용되는 레이아웃을 모아놓은 파일
  현재는 로그인 유무
  상단레이아웃 StarLive클릭 시 메인페이지이동,
  프로필 클릭시 팔로잉, 팔로워목록 볼 수있는 profile.js파일로 이동한다.*/}

  const AppLayout = ({ children }) => {
    const { isLoggedIn, me } = useSelector(state => state.user);
    const dispatch = useDispatch();

    useEffect(() => {
      if (!me) {
        dispatch({
          type: LOAD_USER_REQUEST,
        });
      }
    }, []);

  return (
  <div>
    <Menu mode="horizontal">
      <Menu.Item key="home"><Link href="/"><a>StarLive</a></Link></Menu.Item>
      <Menu.Item key="profile"><Link href="/profile"><a>프로필</a></Link></Menu.Item>
      <Menu.Item key="mail">
        <Input.Search enterButton style={{verticalAlign: 'middle'}}/>
      </Menu.Item>
    </Menu>
    {/*xs:모바일, sm:작은화면, md: 중간화면, lg: 큰화면*/}
    <Row gutter={8}>
      <Col xs={24} md={6}>
      {/*로그인구현
        isLoggedIn값이 true이면 유저의 정보가 보이고, false이면 로그인창이 보이는 삼항연산자*/}
        {isLoggedIn
             ? <UserProfile />
             : <LoginForm />}
      </Col>
      <Col xs={24} md={12}>
        {children}
      </Col>
      <Col xs={24} md={6}>
        <Link href="" ><a target="_blank">Made by Joey!</a></Link>
      </Col>
    </Row>
  </div>
  );
};

AppLayout.propTypes = {
  children: PropTypes.node,
};

export default AppLayout;
